@font-face{font-family:"Pretendard";src:url("https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff") format("woff");font-weight:400;font-style:normal}*{margin:0;padding:0}body{position:fixed;width:100%;height:100%;background-color:#343a40;font-family:"Pretendard","맑은 고딕";display:flex;align-items:center;justify-content:space-between;justify-content:center}.App{width:100vw;max-width:700px;height:100%;min-height:320px;color:#f5f5f5}.App header{width:83%;height:32px;margin:auto;display:flex;align-items:center;justify-content:space-between}.App header .lifebox{width:60px;display:flex;align-items:center;justify-content:space-between}.App header .lifebox .life{font-size:18px}.App header .spacer{width:90px}.board{width:100%;height:100vw;max-height:700px;margin:10px auto 0;display:flex;flex-wrap:wrap;justify-content:space-evenly;align-content:space-around}.board .box{display:inline-block;padding:.02%;background-clip:content-box;border-radius:3px;cursor:pointer}.board.ending{height:70vw;max-height:350px;text-align:center;flex-direction:column;justify-content:center}.board.ending p{font-size:28px;margin:20px auto}.board.ending p .stage{font-size:20px}.board.ending .btn{width:120px;height:40px;line-height:40px;margin:20px auto;font-size:28px;color:#f5f5f5;border-radius:5px;cursor:pointer;transition:background .4s,color .5s}.board.ending .btn:hover{color:#343a40;background-color:#f5f5f5}